const ObjectSentenceGame = () => {
  const [current, setCurrent] = useState(0);
  const [selected, setSelected] = useState("");
  const [score, setScore] = useState(0);
  const [isFinished, setIsFinished] = useState(false);
  const [questions, setQuestions] = useState(questions1);
  const [idModal, setIdModal] = useState("question1");
  const [showMeaning, setShowMeaning] = useState(false);

  const handleAnswer = (option) => {
    setSelected(option);
    const isCorrect = option === questions[current].correct;

    if (isCorrect) {
      setScore((prev) => prev + 1);
      setShowMeaning(true);
    }

    setTimeout(() => {
      setSelected("");
      setShowMeaning(false);
      if (current + 1 < questions.length) {
        setCurrent((prev) => prev + 1);
      } else {
        setIsFinished(true);
      }
    }, 4000);
  };

  const resetGame = () => {
    setCurrent(0);
    setScore(0);
    setSelected("");
    setIsFinished(false);
    setShowMeaning(false);
  };

  const progress = (current / questions.length) * 100;

  const stylebg = {
    backgroundColor: "#fffae6",
    borderRadius: "15px",
  };

  const questionsqq = (e, i) => {
    setQuestions(e);
    setIdModal(i);
    setCurrent(0);
    setSelected("");
    setScore(0);
    setIsFinished(false);
    setShowMeaning(false);
  };

  return (
    <div
      className="mt-5 text-center border border-3 border-info py-5 shadow-lg"
      style={{ borderRadius: "25px", backgroundColor: "#fff3cd" }}
    >
      {/* Start modal */}
      <div className="p-3">
        <button
          type="button"
          className="btn btn-lg btn-info fw-bold shadow"
          data-bs-toggle="modal"
          data-bs-target={"#" + idModal}
          style={{ borderRadius: "50px" }}
        >
           
        </button>
        <div
          className="modal fade"
          id={idModal}
          tabIndex="-1"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog">
            <div className="modal-content">
              <div
                className="modal-header"
                style={{ backgroundColor: "#ffeb99" }}
              >
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
                <h1 className="modal-title fs-5 fw-bold">  </h1>
              </div>
              <div className="modal-body">
                <div className="d-flex flex-wrap">
                  <button
                    className="btn shadow-sm border mx-2 fw-bold"
                    style={stylebg}
                    onClick={() => questionsqq(questions1, "question1")}
                  >
                      
                  </button>
                  <button
                    className="btn shadow-sm border mx-2 fw-bold"
                    style={stylebg}
                    onClick={() => questionsqq(action1, "question1")}
                  >
                       
                  </button>
                  <button
                    className="btn shadow-sm border mx-2 fw-bold"
                    style={stylebg}
                    onClick={() => questionsqq(weather, "question2")}
                  >
                       
                  </button>
                  <button
                    className="btn shadow-sm border mx-2 fw-bold"
                    style={stylebg}
                    onClick={() => questionsqq(illnessSignsQuestions, "question3")}
                  >
                      
                  </button>
                  <button
                    className="btn shadow-sm border mx-2 fw-bold"
                    style={stylebg}
                    onClick={() => questionsqq(pantry, "question4")}
                  >
                     
                  </button>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-success"
                  data-bs-dismiss="modal"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <h2 className="mb-3 text-primary fw-bold" style={{ fontFamily: "Comic Sans MS" }}>
           
      </h2>

      {isFinished ? (
        <>
          <h3 className="text-success mb-4 fw-bold">   !</h3>
          <h4 className="mb-4">
             : {score}  {questions.length} 
          </h4>
          <button
            className="btn btn-warning btn-lg shadow"
            style={{ borderRadius: "50px" }}
            onClick={resetGame}
          >
              
          </button>
        </>
      ) : (
        <>
          <div className="mb-3">
            <strong> : {score}</strong>  {questions.length}
          </div>

          <div className="progress mb-4" style={{ height: "25px", borderRadius: "20px" }}>
            <div
              className="progress-bar bg-success fw-bold"
              role="progressbar"
              style={{ width: `${progress}%`, borderRadius: "20px" }}
              aria-valuenow={progress}
              aria-valuemin="0"
              aria-valuemax="100"
            >
              {Math.round(progress)}%
            </div>
          </div>

          <div
            className="container shadow-sm border p-2 mb-3"
            style={{
              width: "600px",
              height: "400px",
              borderRadius: "20px",
              backgroundColor: "#e0f7fa",
            }}
          >
            <img
              src={questions[current].image}
              alt="object"
              className="img-thumbnail"
              style={{
                width: "100%",
                height: "100%",
                borderRadius: "20px",
                border: "5px solid #ffccbc",
              }}
            />
          </div>

          <div className="row justify-content-center">
            {questions[current].options.map((opt, index) => (
              <button
                key={index}
                className={`btn h1 m-2 col-10 col-md-4 shadow fw-bold ${
                  selected
                    ? opt === questions[current].correct
                      ? "btn-success"
                      : opt === selected
                      ? "btn-danger"
                      : "btn-secondary"
                    : "btn-outline-primary"
                }`}
                style={{
                  borderRadius: "50px",
                  fontFamily: "Comic Sans MS",
                  fontSize: "1.5rem",
                }}
                onClick={() => handleAnswer(opt)}
                disabled={!!selected}
              >
                {opt}
              </button>
            ))}
          </div>

          {showMeaning && (
            <div
              className="alert alert-success mt-4 fw-bold shadow-lg"
              style={{
                fontSize: "1.3rem",
                borderRadius: "15px",
                fontFamily: "Comic Sans MS",
                backgroundColor: "#d1f2eb",
              }}
            >
               {questions[current].word} = {questions[current].meaning}
            </div>
          )}

          <div className="mt-4">
            <button
              className="btn btn-warning btn-lg shadow"
              style={{ borderRadius: "50px" }}
              onClick={resetGame}
            >
                
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default ObjectSentenceGame;



